<template>
  <div>
    <van-sticky>
      <van-nav-bar title="向商家付款"/>
    </van-sticky>
    <div class="scanTop">
      <div class="leftEl">
        <div>
          付款给商户
        </div>
        <div>
          新安农商银行
        </div>
      </div>
      <div class="rightEl">
        <img src="../../assets/imgs//mall/logo.png" alt="" />
      </div>
    </div>
    <!-- 付款界面 -->
    <div class="scanBtm">
      <div class="moneyWrap">
        <div class="moneyTop">付款金额</div>
        <van-field
          v-model="money"
          clearable
          label="￥"
          placeholder="请输入付款金额"
        />
        <div class="tips">
          温馨提示:钱将实时付到商户账户，无法退还。
        </div>
      </div>
      <div class="gap"></div>
      <van-cell-group>
        <van-cell title="积分抵扣"  @click="showIntegral" >
           <span>{{integralInput||0}}积分</span>
        </van-cell>
        <van-cell title="佣金抵扣" @click="showCommission" >
            <span>{{commissionInput||0}}佣金</span>
        </van-cell>
        <van-cell title="优惠券" to="myCoupon" >
            <span  v-if="!ticketChoose.price">请选择优惠券</span>
            <span v-else>已优惠{{ticketChoose.price}}元</span>
        </van-cell>
      </van-cell-group>
      <van-number-keyboard
        v-model="money"
        extra-key="."
        @input="onInput"
        close-button-text="完成"
        @delete="onDelete"
        @close="toPay"
      />
    </div>
    <!-- 抵扣积分 -->
    <van-dialog
      v-model="integralShow"
      title="积分抵扣"
      showCancelButton
      confirmButtonColor="#DE4F53"
      :beforeClose="sendIntegral"
    >
      <p class="dialogTips">({{ config }}积分抵扣1元)</p>
      <van-cell-group>
        <van-field
          v-model="myIntegral"
          label="当前可用积分"
          disabled
          input-align="right"
        />
        <van-field
          v-model="integralInput"
          label="积分抵扣"
          input-align="right"
          type="tel"
          οnkeyup="this.value=(this.value.match(/\d+(\.\d{0,2})?/)||[''])[0];"
        />
      </van-cell-group>
    </van-dialog>
    <!-- 佣金抵扣 -->
    <van-dialog
      v-model="commissionShow"
      title="佣金抵扣"
      showCancelButton
      confirmButtonColor="#DE4F53"
      :before-close="sendCommission"
    >
      <p class="dialogTips">(1佣金抵扣1元)</p>
      <van-cell-group>
        <van-field
          v-model="myCommission"
          label="当前可用佣金"
          disabled
          input-align="right"
        />
        <van-field
          v-model="commissionInput"
          label="佣金抵扣"
          input-align="right"
        />
      </van-cell-group>
    </van-dialog>
  </div>
</template>

<script>
import { NumberKeyboard } from "vant";
import {
  myIntegral,
  myCommission,
  scanPay,
  integralConfig
} from "@/api/personal";
import {
  reduceList
} from "@/api/mall";
export default {
  data() {
    return {
      money: "",
      myIntegral: "",
      integralShow: false, //积分
      integralInput: "",
      myIntegral: "",
      commissionShow: false, //佣金
      commissionInput: "",
      myCommission: "",
      config: 0,
      ticketChoose: {},
      config: "",
      dec: 0
    };
  },
  created() {
    localStorage.setItem('bindType',2);
    // integralConfig().then(res => {
    //   this.config = res.dec;
    // });
    if (!this.$route.query.chooseTicket) {
    } else {
      this.ticketChoose = this.$route.query.chooseTicket;
    }
  },
  methods: {
    showIntegral() {
      this.integralShow = true;
      this.getIntegral();
    },
    // 获取积分
    getIntegral() {
      myIntegral().then(res => {
        this.myIntegral = res.integral;
      });
    },
    sendIntegral(action, done) {
      if (action == "confirm") {
        if (Number(this.integralInput) > Number(this.myIntegral)) {
          this.$toast("填写的抵扣积分不能大于拥有的积分！");
          done(false);
        } else {
          done();
        }
      } else {
        done();
      }
    },
    // 获取佣金
    showCommission() {
      this.commissionShow = true;
      this.getCommission();
    },
    getCommission() {
      myCommission().then(res => {
        this.myCommission = res.commission;
      });
    },
    sendCommission(action, done) {
      if (action == "confirm") {
        if (parseInt(this.commissionInput) > parseInt(this.myCommission)) {
          this.$toast("填写的抵扣佣金不能大于拥有的佣金！");
          done(false);
        } else {
          done();
        }
      } else {
        done();
      }
    },
    onInput(value) {},
    onDelete() {},
    toPay() {
      if (!this.money) {
        this.$toast("请输入付款金额!");
      } else {
        scanPay(
          this.money,
          this.ticketChoose.id || 0,
          this.integralInput || 0,
          this.commissionInput || 0
        ).then(res => {
          WeixinJSBridge.invoke(
            "getBrandWCPayRequest",
            {
              appId: res.appId,
              timeStamp: res.timeStamp,
              nonceStr: res.nonceStr,
              package: res.package,
              signType: res.signType,
              paySign: res.paySign
            },
            function(res) {
              if (res.err_msg == "get_brand_wcpay_request:ok") {
                this.$toast("支付成功");
                this.orderList();
              }
            }
          );
        });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
  .dialogTips{
     text-align: center;
     color:rgb(222, 79, 83);
     font-size:12px;
  }
.van-nav-bar {
  background: #f2f2f2;
}
.scanTop {
  background: #28A6FF;
  width: 100%;
  overflow: hidden;
  color: #333333;
  box-sizing: border-box;
  padding: 11px 38px;
  font-size: 18px;
  img {
    width: 90px;
    height: 90px;
  }
  .leftEl {
    margin-top: 26px;
    div:last-child {
      font-size: 15px;
      margin-top: 4px;
    }
  }
}
.scanBtm {
  width: 100%;
  height: 491px;
  background: #fff;
  border-radius: 12px 12px 0 0;
  margin-top: -10px;
  font-size: 15px;
  color: #333;
}
.moneyWrap {
  width: 100%;
  font-size: 18px;
  box-sizing: border-box;
  padding: 8px 15px 0;
  /deep/ .van-field__label {
    width: 1rem;
    font-size: 25px;
  }
}

.tips {
  color: #fb4e54;
  font-size: 11px;
  margin: 8.5px 0 10.5px;
}
.gap {
  width: 100%;
  height: 5px;
  background: #f5f5f5;
}
/deep/ .van-number-keyboard {
  display: block !important;
}
</style>
